html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  height: 100%;
}

*, :before, :after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

@font-face {
  font-family: Neue Machina;
  font-display: swap;
  src: url("NeueMachina-Regular.4e5e4d21.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Neue Machina;
  font-display: swap;
  src: url("NeueMachina-Ultrabold.91912f9a.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
}

:root {
  --bg-1: #f0f0f0;
  --bg-2: #151515;
  --white: #fff;
  --black: #151515;
  --transition: .3s;
  --transition-long: 1s;
  --font-family: "Neue Machina", sans-serif;
  --paddingInline: 80px;
}

@media (width <= 1439px) {
  :root {
    --paddingInline: 32px;
  }
}

@media (width <= 767.98px) {
  :root {
    --paddingInline: 16px;
  }
}

html {
  scroll-padding-top: 68px;
}

.body {
  font-family: var(--font-family);
  color: var(--black);
  background-color: var(--bg-1);
  border: 0;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
  position: relative;
  overflow-x: clip;
}

.body.menu-active {
  overflow-y: hidden;
}

.visually-hidden {
  white-space: nowrap !important;
  clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  border: 0 !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  overflow: hidden !important;
}

.header {
  z-index: 100;
  transition: background-color var(--transition);
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.header.header--scrolling {
  background-color: var(--bg-1);
}

@media (width <= 959.98px) {
  .header:before {
    content: "";
    z-index: 200;
    background-color: var(--bg-1);
    transition: transform var(--transition);
    position: absolute;
    inset: 0;
    transform: translate(100%);
  }

  .menu-active .header:before {
    transform: translate(0);
  }
}

.header__container {
  max-width: calc(1510px + var(--paddingInline) * 2);
  padding-inline: var(--paddingInline);
  --gap: 20px;
  align-items: center;
  column-gap: var(--gap);
  transition: min-height var(--transition);
  min-height: 110px;
  margin: 0 auto;
  display: flex;
}

.header--scrolling .header__container {
  min-height: 60px;
}

@media (width <= 767.98px) {
  .header__container {
    min-height: 90px;
  }
}

@media (width <= 959.98px) {
  .header__container {
    justify-content: space-between;
  }
}

.header__logo {
  z-index: 200;
  position: relative;
}

.header__img-logo {
  max-width: 100%;
  display: block;
}

.menu {
  flex-grow: 1;
}

@media (width <= 959.98px) {
  .menu {
    background-color: var(--bg-1);
    padding: 150px 0 30px;
    transition: transform .4s linear;
    position: fixed;
    inset: 0;
    overflow-y: auto;
    transform: translateX(100%);
  }

  .menu.menu-active {
    transform: translateX(0);
  }
}

.menu__list {
  align-items: center;
  column-gap: var(--gap);
  margin-block: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

@media (width <= 959.98px) {
  .menu__list {
    justify-content: flex-start;
    align-items: center;
    row-gap: var(--gap);
    flex-flow: column;
    margin-bottom: 30px;
  }
}

.menu__link {
  white-space: nowrap;
  color: inherit;
  padding: 17px 20px;
  line-height: 1;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.menu__link:before {
  content: "";
  background-color: var(--black);
  transition: transform var(--transition);
  width: calc(100% - 40px);
  height: 2px;
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%)scale(0);
}

@media (any-hover) {
  .menu__link:hover:before {
    transform: translateX(-50%)scale(1);
  }
}

@media (width <= 959.98px) {
  .menu__link {
    font-size: 24px;
  }
}

.button-block {
  z-index: 200;
  align-items: center;
  column-gap: var(--gap);
  display: flex;
  position: relative;
}

.button-block--menu {
  z-index: 200;
  display: none;
  position: relative;
}

@media (width <= 574.98px) {
  .button-block--menu {
    justify-content: center;
    display: flex;
  }
}

.button-block__button {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  transition: background-color var(--transition), color var(--transition);
  border: none;
  min-height: 52px;
  padding-inline: 20px;
}

@media (width <= 574.98px) {
  .button-block__button {
    display: none;
  }
}

.button-block__button--white {
  background-color: var(--white);
}

@media (any-hover) {
  .button-block__button--white:hover {
    background-color: var(--black);
    color: var(--white);
  }
}

.button-block__button--black {
  background-color: var(--black);
  color: var(--white);
}

@media (any-hover) {
  .button-block__button--black:hover {
    background-color: var(--white);
    color: var(--block);
  }
}

@media (width <= 574.98px) {
  .button-block__button--menu {
    font-size: 24px;
    display: block;
  }
}

.burger-btn {
  display: none;
}

@media (width <= 959.98px) {
  .burger-btn {
    z-index: 200;
    background-color: #0000;
    border: 0;
    width: 30px;
    height: 20px;
    display: block;
    position: relative;
  }
}

@media (width <= 959.98px) and (any-hover) {
  .burger-btn:hover {
    cursor: pointer;
  }
}

@media (width <= 959.98px) {
  .burger-btn span, .burger-btn:before, .burger-btn:after {
    content: "";
    background-color: var(--black);
    width: 100%;
    height: 2px;
    transition: all .3s;
    position: absolute;
    right: 0;
  }

  .burger-btn:before {
    top: 0;
  }

  .burger-btn:after {
    bottom: 0;
  }

  .burger-btn span {
    top: calc(50% - 1px);
  }
}

.burger-btn.menu-active span {
  width: 0;
}

.burger-btn.menu-active:before {
  top: calc(50% - 1px);
  transform: rotate(-45deg);
}

.burger-btn.menu-active:after {
  bottom: calc(50% - 1px);
  transform: rotate(45deg);
}

