:root {
	--bg-1: #f0f0f0;
	--bg-2: #151515;
	--white: #fff;
	--black: #151515;
	--transition: 0.3s;
	--transition-long: 1s;
	--font-family: "Neue Machina", sans-serif;
	--paddingInline: 80px;
	@media (max-width: 1439px) {
		--paddingInline: 32px;
	}
	@include m {
		--paddingInline: 16px;
	}
}

html {
	scroll-padding-top: 68px;
}

.body {
	position: relative;
	overflow-x: clip;
	min-height: 100%;
	margin: 0;
	padding: 0;
	border: 0;
	font-family: var(--font-family);
	font-size: 18px;
	font-weight: 400;
	line-height: 1.5;
	color: var(--black);
	background-color: var(--bg-1);
	&.menu-active {
		overflow-y: hidden;
		// padding-right: 10px;
	}
}

.visually-hidden {
	position: absolute !important;
	width: 1px !important;
	height: 1px !important;
	margin: -1px !important;
	border: 0 !important;
	padding: 0 !important;
	white-space: nowrap !important;
	clip-path: inset(100%) !important;
	clip: rect(0 0 0 0) !important;
	overflow: hidden !important;
}

